
@font-face {
  font-family: fangsong;
  src: url("../assets/WenYueGuTiFangSong-min.ttf") format("truetype");
}
#scroll {
  height: 100%;
  width: 100%;
  background-size: cover;
  background-position: left;
  background-image: url("../assets/landing.jpg");
}
#newhover {
  padding: 40px;
  margin-left: 50%;
  margin-right: 50px;
  height: 95%;
  width: calc(35% - 50px);
  background-color: rgba(255, 255, 255, 0.534);
  /* font-family: fangsong; */
  font-size: 20px;
  text-align: justify;
  text-justify: inter-ideograph;
  /* visibility: visible;
  opacity: 1; */
  overflow: hidden;
}
#imgalign{
  text-align: center;
}
#pho{
  width: 50%;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}
#pho:hover{
  width: 70%;
}





